import * as d3 from 'd3'
import {D3Sel} from '../util/xd3'
import {SVGVisComponent, HTMLVisComponent} from '../util/VisComponent'
import { SimpleEventHandler } from '../util/SimpleEventHandler';

type T = number[]

export class SimpleGraph extends SVGVisComponent<T> {
    cssname = "simple-cake"
    _data: T
    options = {}
    _width = 400;
    _height = 400;

    x
    y
    xaxis
    yaxis
    pad
    legend
    tip


    constructor(d3parent: D3Sel, eventHandler?: SimpleEventHandler, options={}) {
        super(d3parent, eventHandler, options)
        this.base.classed(this.cssname, true)
        this.init()
    }

    init() { 

        this.x = d3.scaleLinear().range([0, this.width()])
        this.y = d3.scaleLinear().range([0, this.height()])

        const width = this._width,
            height = this._height

        this.svg
            .attr("width", this._width)
            .attr("height", this._height)

        this.xaxis = d3.axisBottom(this.x).ticks(3, "s")
        //this.base.append("g")
        // .attr("class", "axis axis--x")
        // .attr("transform", "translate(0," + height + ")")
        // .call(d3.axisBottom(this.x).ticks(3, "s"));
        
        this.yaxis = this.base.append("g")
        .attr("class", "axis axis--y")
        .attr("transform", "translate(0,0)")
        .call(d3.axisLeft(this.y).ticks(3, "s"));
    
        this.base.append("text")
        .text("Iteration")
        .attr("class", "titles")
        .attr("transform", "translate(" + width / 2 + "," + (height + this.pad) + ")")
        .attr("alignment-baseline","hanging"); 
        
        this.base.append("text")
        .text("Cost")
        .attr("class", "titles")
        .attr("transform", "translate(" + -this.pad + "," + height / 2 + ")rotate(-90)")
            .attr("alignment-baseline","baseline");  

        this.base.append("g")
        .attr("class", "legend")
        .attr("transform", "translate(" + (width + this.pad / 2) + ",0)"); 
        
        // this.legend = d3.legendColor()
        // .labelFormat(d3.format(".2g"))
        // .title("Optimizer");

        // this.tip = d3.tip()
        // .attr('class', 'd3-tip')
        // .offset([-10, 0])
        // .html(function(d, i) {
        //     if (i == 1) {
        //         return 'Initial Point <br>(W: ' + d3.format(".2f")(d.b1) + ', b: ' + d3.format(".2f")(d.b0) + ')';
        //     } else {
        //         return 'Ground Truth <br>(W: ' + d3.format(".2f")(d.b1) + ', b: ' + d3.format(".2f")(d.b0) + ')';
        //     }
        // });

        // this.loss.svg.call(this.tip);
        //     .attr("viewBox", "0 0 " + (width + margin.left + margin.right) + " " + (height + margin.top + margin.bottom))
        //     .attr("preserveAspectRatio", "xMidYMid meet")
    }

    data():number[]
    data(val:number[]): this
    data(val?) {
        if (val == null) return this._data
        this._data = val
        return this
    }
}